*,
*:after,
*:before {
  box-sizing: border-box;
}

html,
body {
  @include non-select;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  font-weight: 400;
  @include scroll-bar;
}


ul{
  list-style-type: none;
}

a{
  text-decoration: none;
}


h1, h2, h3, h4, h5, h6{
  line-height: 1.28571429;
  font-weight: normal;
}

img {
  max-width: 100%;
}


li, p {
  line-height: 1.42857143;
}

.body-app{
  max-width: 1680px;
  margin: 0 auto;
  background-color: #eaeaea;
  font-family: 'Open Sans', sans-serif;
  color: $white;
  box-shadow: 0 0 60px 0 rgb(53 54 63 / 50%);
  @include flexible-font-size(14,16);
}



.title-app{
  @include flexible-font-size(28,32);
  font-weight: 400;
}

.color-gray{
  color: $gray;
}


.main-content-app {
  flex: 1 1;
  background-color: $dark;
}

.main-content-catch {
  flex: 1 1;
  background-color: $dark2;
}


.body-catch{
  font-family: Helvetica,Arial,sans-serif;
  color: $white;
  @include flexible-font-size(14,16);
}


.title-catch{
  @include flexible-font-size(28,44);
  font-weight: 700;
}


.body-cheating, .body-find, .body-bust{
  font-family: 'Open Sans', sans-serif;
  color: $white;
  @include flexible-font-size(14,16);
}

.title-cheating{
  @include flexible-font-size(28,36);
  font-weight: bold;
}

.main-content-cheating {
  flex: 1 1;
}


.choice{
  font-size: 13px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
  color: $white;
  text-transform: uppercase;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 16px;
  padding-right: 16px;
  background-color: $dark-gray;
}


.title-bust{
  @include flexible-font-size(24,36);
  font-weight: 700;
  color: #202630;
  text-align: center;
}

.white{
  color: $white;
}

.hidden__tab{
  display: none;
  @media (min-width: $media-tab-lg) {
    display: block;
  }
}